import React from 'react';
import { Link } from 'gatsby';
import { routes } from '../constants';

import SEO from '../components/SEO';

const ThankYouPage = () => {
  return (
    <div className="max-height flex items-center justify-center">
      <div className="contain-sm text-center mt-16">
        <h1 className="text-3xl md:text-4xl md:text-5xl">Thank You!</h1>
        <p className="text-mdgray my-8 md:text-lg">
          Your message has been successfully sent. I will be in contact with you
          as soon as possible!
        </p>
        <Link to={routes.HOME} className="link md:text-lg">
          Back To Home
        </Link>
      </div>
    </div>
  );
};

export default ThankYouPage;
export const Head = () => (
  <SEO
    title="Thank You"
    description="Your message has been successfully sent. I will be in contact with you as soon as possible!"
  />
);
